<template>
    <div class="container">
      <h3 class="tx-c fz500">请登录</h3>
      <div class="wrapper">
        <mt-field  placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
        <mt-field label="验证码" placeholder="请输入验证码" v-model="captcha2">
        <span class="get-code" @click="showCode">{{canClick?'获取验证码' :`还剩${count}s`}}</span>
        </mt-field>
       <mt-button class="login" size="large" type="primary" @click="doLogin">登录</mt-button>
       <div class="pwd-login fr" @click="$router.push('/login')">密码登录</div>
      </div>
      <div class="mask fx fx-ac fx-jc" v-if="mask">
        <div class="code-alert">
          <div class="top">
            <span>请先完成下方的验证</span>
            <span class="fz-20 iconfont icon-shanchudelete30" @click="closeMask"></span>
          </div>
          <div class="code">
            <div class="content fx fx-ac">
              <input class="input" type="text" maxlength="4" v-model="captcha1">
              <img :src="imgCode" width="70" height="30">
              <img @click="changeCode" src="../assets/images/refresh.png" width="25" height="20">
            </div>
            <div class="mistake">{{msg}}</div>
            <mt-button @click="sure" class="btn" size="large" type="primary">确定</mt-button>
          </div>
        </div>
       </div>
    </div>
</template>

<script>
import { checkMobile } from '@/assets/js/util'
import { Toast } from 'mint-ui'
import { randUrl } from '@/assets/js/config'
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      mask: false,
      canClick: true,
      phone: '',
      captcha1: '',
      captcha2: '',
      count: 0,
      msg: '',
      imgCode: randUrl
    }
  },
  mounted () {
    console.log(randUrl)
  },
  methods: {
    ...mapActions(['setUserInfo']),
    doLogin () {
      if (this.phone === '' || this.captcha2 === '') {
        Toast('手机或验证码不能为空')
        return
      }
      if (!checkMobile(this.phone)) {
        Toast('手机号有误')
        return
      }
      this.$api.requestsmsLogin({mobile: this.phone, mCode: this.captcha2, tk: ''}).then(res => {
        if (res.success) {
          console.log(res)
          this.captcha2 = ''
          this.setUserInfo(res)
          this.$router.push('/home')
        } else {
          Toast(res.msg)
        }
      })
    },
    sure () {
      if (this.captcha1 === '') {
        this.msg = '验证码不能为空'
        return
      }
      this.$api.requestchkCode({randCode: this.captcha1}).then(res => {
        console.log(res)
        if (!res.success) {
          this.msg = res.msg
          return
        }
        this.$api.requestSend({mobile: this.phone, flag: 2}).then(res => {
          console.log(res)
          if (res.success) {
            Toast({
              message: '验证码已发送',
              position: 'bottom'
            })
            this.canClick = false
            this.closeMask()
            this.count = 60
            let timer = setInterval(() => {
              this.count--
              if (this.count === 0) {
                this.canClick = true
                clearInterval(timer)
              }
            }, 1000)
          } else {
            Toast(res.msg)
          }
        })
      })
    },
    showCode () {
      if (this.phone === '' || !checkMobile(this.phone)) {
        Toast('手机号有误')
        return
      }
      if (!this.canClick) {
        return
      }
      this.mask = true
    },
    closeMask () {
      this.mask = false
      this.captcha1 = ''
      this.msg = ''
    },
    // 改变图形验证码
    changeCode () {
      this.imgCode = randUrl + '?' + new Date().getTime()
    }
  }
}
</script>

<style scoped lang="scss">
@import '../assets/css/color.scss';
.container{
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom:0;
    background: #fff;
    .wrapper{
     padding: 0 20px;
     font-size:14px;
     .login{
         margin-top: 20px;
         font-size: 16px;
     }
    }
    input{
        font-size: 15px;
        border: 0;
        border-bottom: 1px solid #ccc;
    }
    .foot{
        position: absolute;
        width: 100%;
        bottom: 50px;
        font-size: 12px;
        color: #9c9c9c;
    }
}
.fg{
  margin-top: 10px;
  padding-right: 10px;
  color: #9c9c9c;
  span:active{
    color: #000;
  }
}
.mask{
  z-index: 1;
  position: absolute;
  left:0;top:0;
  width: 100%;
  height: 100%;
  background: $opacity;
  .code-alert{
    width: 225px;
    height: 170px;
    background: #fff;
    .top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 29px;
      padding:0 20px;
      background: #e0e1e5;
      &:first-child{
        font-size: 12px;
      }
    }
    .code{
      margin-top: 30px;
      padding: 0 20px;
      .content{
        height: 30px;
        justify-content: space-between;
        .input{
          display: block;
          width: 74px;
          padding: 5px;
          font-size: 14px;
          border: 1px solid #e0e1e5;
          outline: 0;
        }
      }
      .mistake{
        padding: 10px;
        color: #e00;
        font-size:12px;
      }
      .btn{
        font-size: 16px;
      }
    }
  }
}
 .get-code{
    color: $themeColor;
  }
 .pwd-login{
  margin-top: 10px;
  color: #333;
 }
</style>



